@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
#sidebar-wrapper {
  min-height: 100vh !important;
  width: 100%;
  transition: margin 0.25s ease-out;
}
#page-content-wrapper {
  min-width: 0;
  width: 100%;
  font-family: "Quicksand", sans-serif;
  overflow: auto;
}

.App .container-fluid > .row {
  margin-right: 0 !important;
  height: 100vh;
  -webkit-flex-wrap: nowrap !important;
          flex-wrap: nowrap !important;
}

:root {
  --gradient1: #8860d0;
  --gradient2: #5ab9ea;
  --gradient3: #d2eaf3;
}

/* reset */
a {
  color: #333;
}
a:hover,
a:active {
  color: #333;
  text-decoration: none;
}

input,
a {
  outline: none;
}

h1 {
  margin: 0;
  font-size: 28px;
}
h2 {
  font-size: 24px;
}

ul {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  margin-bottom: 0;
}

/* global style */
.container-fluid {
  padding: 0 !important;
}
.header-underlined {
  text-transform: uppercase;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 40px;
  position: relative;
  padding-bottom: 20px;
}

.header-underlined::before,
.header-underlined::after {
  content: "";
  display: inline-block;
  width: 120px;
  height: 5px;
  background-color: #d2eaf3;
  background-color: var(--gradient3);
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  left: 0;
}
.header-underlined::after {
  z-index: 2;
  width: 40px;
  background-color: #5ab9ea;
  background-color: var(--gradient2);
}

